@media (min-width: 1700px) {
    ul#markdown-toc {
        position: fixed;
        top: 0;
        bottom: 0;
        left: calc(50vw + 410px);
        width: 380px;
        background-color: $main-color-3;
        z-index: 9999;
        font-size: 0.8em;
        overflow-y:scroll;
        overflow-x:hidden;
        border: 0px solid;
        line-height: 1.5;
    }
}

#markdown-toc {
    border: 1px solid;
    padding: 10px;
    padding-left: 30px;
    background-color: $main-color-3;
    line-height: 1.5;
}

